<template>
    <div class="task_detail">
        <!-- operations -->
        <el-row id="top-buttons">
            <el-button type="primary" v-on:click='$router.push("/task_new")' class="oper-btn">
                
                <i class="el-icon-s-promotion"></i>
            </el-button>

            <el-button type="info" v-on:click="changeAlias" class="oper-btn">
                
                <i class="el-icon-edit"></i>
            </el-button>

            <el-button type="success" v-on:click="downloadOutput(task_info.task_id)" class="oper-btn">
                
                <i class="el-icon-download"></i>
            </el-button>

            <el-popconfirm title="" class="el-popconfirm oper-btn" @confirm="stopTask">
                <el-button type="warning" slot="reference">
                    
                    <i class="el-icon-close"></i>
                </el-button>
            </el-popconfirm>

            <el-popconfirm title="" class="el-popconfirm oper-btn" @confirm="deleteTask">
                <el-button type="danger" slot="reference">
                    
                    <i class="el-icon-delete"></i>
                </el-button>
            </el-popconfirm>
        </el-row>

        <el-card class="task_info_card" v-if="task_info_display_control">
          <div slot="header" class="clearfix">
            <b></b>
          </div>
          <table border=0 class="task_info_table" style="word-break:break-all;word-wrap:break-word;">
            <tr><td>ID:{{ task_info.task_id }}</td><td><pre>&#9;</pre></td>
              <td>:{{ task_info.party_name }}</td><td><pre>&#9;</pre></td>
              <td>:{{ task_info.party_names }}</td><td><pre>&#9;</pre></td>
              <td>:<span v-bind:class="task_info.status">{{ task_info.status | task_status }}</span></td>
            </tr>
            <tr>
              <td>:{{ task_info.create_time }}</td><td><pre>&#9;</pre></td>
              <td>:{{ task_info.update_time }}</td><td><pre>&#9;</pre></td>
              <td><a class="show-modal" v-on:click="task_json_visible=true;"></a></td><td><pre>&#9;</pre></td>
              <td>:{{ task_info.alias || "" }}</td>
            </tr>
            <tr v-for="(item,index) in this.metrics_task_info" v-bind:key="index">
              <td v-for="(value,key,index2) in item" v-bind:key="index2" colspan="2">{{key}}:{{value}}</td>
            </tr>
          </table>
        </el-card>

        <el-row :gutter="10">
            <!-- meta -->
            <el-col v-if="!task_info_display_control" :xs="24" :sm="24" :md="24" :lg="6">
                <el-card class="box-card card">
                    <div slot="header" class="clearfix">
                        <b></b>
                    </div>
                    <p>ID{{ task_info.task_id }}</p>
                    <p>{{ task_info.party_name }}</p>
                    <p>{{ task_info.party_names }}</p>
                    <p><span v-bind:class="task_info.status">{{ task_info.status | task_status }}</span></p>
                    <p>{{ task_info.alias || "" }}</p>
                    <p>{{ task_info.create_time }}</p>
                    <p>{{ task_info.update_time }}</p>
                    <p><a class="show-modal" v-on:click="task_json_visible=true;"></a></p>
                    <div v-if="metrics !== null && metrics.length > 0">
                        <p></p>
                        <div v-for="(m, index) in metrics" v-bind:key='index'>
                            <div v-for="(value,key,index2) in m" v-bind:key='index2'>
                                <p>{{key}}={{value}}</p>
                            </div>
                        </div>
                    </div>
                </el-card>
            </el-col>

            <!-- charts -->
            <el-col v-if="!task_info_display_control" :xs="24" :sm="24" :md="24" :lg="9">
                <Procedure class="cards" v-bind:procedure="procedure" v-bind:task_json_dict="task_json_dict"></Procedure>
            </el-col>

            <!-- charts -->
            <el-col v-if="!task_info_display_control" :xs="24" :sm="24" :md="24" :lg="9">
                <LossAndTime class="cards" v-bind:loss_and_time="loss_and_time"></LossAndTime>
            </el-col>

            <!-- charts -->
            <el-col v-if="task_info_display_control" :xs="24" :sm="24" :md="24" :lg="8">
              <Procedure class="cards" v-bind:procedure="procedure" v-bind:task_json_dict="task_json_dict"></Procedure>
            </el-col>

            <!-- charts -->
            <el-col v-if="task_info_display_control" :xs="24" :sm="24" :md="24" :lg="8">
              <LossAndTime class="cards" v-bind:loss_and_time="loss_and_time"></LossAndTime>
            </el-col>

            <!-- charts -->
            <el-col v-if="task_info_display_control" :xs="24" :sm="24" :md="24" :lg="8">
              <TrainAndValidate class="cards" v-bind:train_and_validate="train_and_validate"></TrainAndValidate>
            </el-col>

        </el-row>

        <!-- error logs -->
        <el-row v-if="error_msg.length > 0">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <b></b>
                </div>
                <div id="error_msg">
                    <div v-for="(msg, index) in error_msg" v-bind:key='index'>
                        <span class="line-number">{{ index+1 }}</span> {{ msg }}
                    </div>
                </div>
            </el-card>
        </el-row>

        <!-- logs -->
        <el-row>
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <b></b>
                    <span class="log-span">5</span>
                    <el-button
                        icon="el-icon-refresh-right"
                        size="mini"
                        class="log-span"
                        v-on:click="refreshLog"
                        circle>
                    </el-button>
                    <span id="scroll-switch">
                        
                        <el-switch
                            v-model="scroll_to_bottom"
                            class="log-span" >
                        </el-switch>
                    </span>
                </div>
                <div id="task_log">
                    <div v-for="(task_log, index) in task_logs" v-bind:key='index'>
                        <span class="line-number">{{ index+1 }}</span> {{ task_log }}
                    </div>
                </div>
            </el-card>
        </el-row>

        <el-dialog
            title=""
            :visible.sync="task_json_visible"
            width="80%" >
            <vue-json-editor
                v-model="task_json_dict"
                mode="code"
                lang="zh"
                id="task-json-dialog" />
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" v-on:click="copyTaskJSON" ></el-button>
                <el-button v-on:click="task_json_visible=false"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import vueJsonEditor from 'vue-json-editor';
    import { use } from "echarts/core";
    import {
        TitleComponent,
        ToolboxComponent,
        TooltipComponent,
        GridComponent,
        LegendComponent,
        DataZoomComponent,
        MarkAreaComponent
    } from 'echarts/components';
    import {
        LineChart,
        GraphChart
    } from 'echarts/charts';
    import {
        CanvasRenderer
    } from 'echarts/renderers';

    // sub-components
    import LossAndTime from "./TaskDetail/LossAndTime";
    import TrainAndValidate from "./TaskDetail/TrainAndValidate";
    import Procedure from "./TaskDetail/Procedure";

    use([
        TitleComponent,
        ToolboxComponent,
        TooltipComponent,
        GridComponent,
        LegendComponent,
        DataZoomComponent,
        MarkAreaComponent,
        LineChart,
        GraphChart,
        CanvasRenderer
    ]);

    export default {

        name: 'TaskDetail',
        data() {
            return {
                // 
                task_info: {},
                // 
                task_json_dict: [],
                // 
                task_logs: [],
                // 
                loss_and_time: {
                    loss_list: [],
                    time_list: []
                },
                //train_auc & validate_auc
                train_and_validate:{
                    train_auc: [],
                    validate_auc:[],
                    x_axis:[]
                },
                task_info_display_control: false,
                // metrics
                metrics: [],
                // metrics4task_info
                metrics_task_info:[],
                // 
                error_msg: [],

                procedure: '',
                // 
                task_json_visible: false,
                // 
                scroll_to_bottom: true,
                // 
                timer: null,
            };
        },
        components: {
            vueJsonEditor,
            LossAndTime,
            Procedure,
            TrainAndValidate
        },
        methods: {
            // 
            refreshLog() {
                if (typeof(this.$route.query.task_id) === "undefined") {
                    clearTimeout(this.timer);
                    return;
                }
                // get logs from server
                this.$axios.get("/board/task/"+this.$route.query.task_id+"?type=dlog&start_offset="+this.task_logs.length, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                .then(resp => {
                    this.task_logs = this.task_logs.concat(resp.data.data.log_list);
                    this.task_info.status = resp.data.data.task_status;
                    if (this.scroll_to_bottom) {
                        setTimeout(this.scrollToBottom, 1000);
                    }
                    if (this.task_info.status === "finish" || this.task_info.status === "error") {
                        clearTimeout(this.timer);
                    }
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    clearTimeout(this.timer);
                });

                // refresh task_progress
                this.$axios.get("/board/task_progress/"+this.$route.query.task_id,{
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                .then(resp => {
                    this.procedure = resp.data.data.progress;
                    this.loss_and_time.loss_list = resp.data.data.losses;
                    this.loss_and_time.time_list = resp.data.data.times;
                    this.metrics = resp.data.data.metrics;
                    let ta = resp.data.data.train_auc;
                    let va = resp.data.data.validate_auc;
                    //train_auc&validate_auc
                    this.train_and_validate.train_auc=[];
                    this.train_and_validate.validate_auc=[];
                    this.train_and_validate.x_axis=[];
                    for(let i=0;i<ta.length;i++){
                      if((ta[i] !== 0) && (va[i]!==0)){
                        this.train_and_validate.train_auc.push(ta[i]);
                        this.train_and_validate.validate_auc.push(va[i]);
                        this.train_and_validate.x_axis.push(i.toString());
                      }
                    }
                    this.task_info_display_control = this.train_and_validate.train_auc.length > 0
                        && this.train_and_validate.validate_auc.length > 0;
                    //4task info
                    this.metrics_task_info = [];
                    let count = 0;
                    let metrics_row = {};
                    for(let prop in this.metrics){
                      // eslint-disable-next-line no-prototype-builtins
                      if(this.metrics.hasOwnProperty(prop)){
                        metrics_row[prop] = this.metrics[prop];
                        if(count===3){
                          this.metrics_task_info.push(metrics_row);
                          metrics_row = {};
                        }
                        count = (count + 1)%4;
                      }
                    }
                    if(!(JSON.stringify(metrics_row) === '{}')){
                      this.metrics_task_info.push(metrics_row);
                    }
                    if (resp.data.data.error_msg !== null) {
                        this.error_msg = resp.data.data.error_msg.split('\n');
                    }
                })
                .catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    clearTimeout(this.timer);
                });
            },

            // 
            copyTaskJSON() {
                let that = this;
                this.$copyText(this.task_info.task_chain)
                    .then(function () {
                        that.$notify.success({
                            title: '',
                            message: ''
                        });
                    });
                this.task_json_visible=false;
            },

            // 
            scrollToBottom() {
                let container = this.$el.querySelector("#task_log");
                container.scrollTop = container.scrollHeight;
            },

            // 
            changeAlias() {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputValue: this.task_info.alias,
                }).then(({ value }) => {
                    if (value === null) {
                        this.$notify.error({
                            title: '',
                            message: ''
                        });
                        return;
                    }
                    // send post to backend
                    this.$axios.patch("/board/task/"+this.$route.query.task_id, {
                        'field': {
                            'alias': value
                        },
                    }, {
                        headers: {
                            'auth': window.localStorage.getItem('jeddak_token')
                        }
                    })
                    // eslint-disable-next-line no-unused-vars
                    .then(resp => {
                        this.$notify.success({
                            title: '',
                            message: ''
                        });
                        this.task_info.alias = value;
                    }).catch(err => {
                        console.log(err);
                        this.$notify.error({
                            title: '',
                            message: ''
                        });
                    });
                });
            },

            // 
            stopTask() {
                if (this.task_info.status !== 'run') {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    return;
                }
                // send post to backend
                this.$axios.patch("/board/task/"+this.$route.query.task_id, {
                    'type': 'stop',
                }, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                    this.$notify.success({
                        title: '',
                        message: resp.data.data.message
                    });
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    clearTimeout(this.timer);
                });
            },

            // 
            deleteTask() {
                if (this.task_info.status === 'run') {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    return;
                }
                // send post to backend
                this.$axios.delete("/board/task/"+this.$route.query.task_id, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                    this.$notify.success({
                        title: '',
                        message: ''
                    });
                    clearTimeout(this.timer);
                    this.$router.back();
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    clearTimeout(this.timer);
                });
            },

            // 
            downloadOutput(task_id) {
                const url = "/board/task/"+task_id+"?type=output";
                const file_name = task_id + ".csv"
                this.$axios({
                    url:url,
                    method:'get',
                    responseType: 'blob',
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                }).then(res => {
                    let blob = new Blob([res.data]);
                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob, file_name);
                    } else {
                        let link = document.createElement("a");
                        let evt = document.createEvent("HTMLEvents");
                        evt.initEvent("click", false, false);
                        link.href = URL.createObjectURL(blob);
                        link.download = file_name;
                        link.style.display = "none";
                        document.body.appendChild(link);
                        link.click();
                        window.URL.revokeObjectURL(link.href);
                    }
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '',
                        message: '1.data_saver 2.'
                    });
                });
            },
        },
        mounted() {
            this.$axios.get("/board/task/"+this.$route.query.task_id+"?type=detail", {
                headers: {
                    'auth': window.localStorage.getItem('jeddak_token')
                }
            })
            .then(resp => {
                this.task_info = resp.data.data;
                this.task_info.create_time = new Date(this.task_info.create_time+'Z').toLocaleString();
                this.task_info.update_time = new Date(this.task_info.update_time+'Z').toLocaleString();
                this.task_json_dict = JSON.parse(this.task_info.task_chain);

                // start timer
                if (typeof(this.task_info.task_id) !== "undefined") {
                    this.refreshLog();
                    this.timer = setInterval(this.refreshLog, 5000);
                }
            }).catch(err => {
                console.log(err.response);
                if (err.response.status === 400 && err.response.data.message === 'task_id not found') {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    return;
                }
                this.$notify.error({
                    title: '',
                    message: ''
                });
            });
        }
    }
</script>

<style scoped>
    .card {
        margin-bottom: 10px;
        height: 590px;
    }
    #task_log, #error_msg {
        height: 400px;
        overflow: scroll;
    }
    .el-popconfirm {
        margin-left: 10px;
    }
    .log-span {
        margin-left: 10px;
    }
    .show-modal {
        color: grey;
        cursor: pointer;
    }
    #task-json-dialog {
        height: 600px;
    }
    .line-number {
        color: green;
        margin-right: 10px;
    }
    #scroll-switch {
        float: right;
    }
    .oper-btn {
      margin-left: 0;
      margin-right: 5px;
    }
    .task_info_card{
      margin-bottom: 10px;
    }
    .cards{
      margin-bottom: 10px;
    }
</style>


<style>
    .jsoneditor-vue {
        height: 600px;
    }
</style>
