<template>
    <div class="procedure">
        <el-card class="box-card card">
            <v-chart class="chart" :option="data" />
        </el-card>
    </div>
</template>

<script>
    export default {
        name: 'Procedure',
        props: ['procedure', 'task_json_dict'],
        data() {
            return {
            }
        },
        methods: {
            // 
            getTaskColor(finished_module_num, this_index) {
                if (this_index <= finished_module_num) {
                    return 'lightblue'
                } else if (this_index === finished_module_num+1) {
                    return 'lightgreen'
                }
                return 'lightgrey'
            },
            // 
            getTaskModules() {
                let finished_module_num = this.procedure;
                let module_length = this.task_json_dict.length;
                let module_series_data = [];
                for (let i = 1; i < module_length; i++) {
                    module_series_data.push({
                        name: 'step.'+i.toString()+' '+this.task_json_dict[i].task_type,
                        // x: 300+150*Math.cos(2*Math.PI / (module_length) * (i-1)),
                        // y: 300+150*Math.sin(2*Math.PI / (module_length) * (i-1)),
                        y: 0,
                        x: 100 * i,
                        itemStyle: {
                            color: this.getTaskColor(finished_module_num, i)
                        }
                    })
                }
                return module_series_data;
            },
            // 
            getModuleLinks() {
                let module_length = this.task_json_dict.length - 1;
                let module_series_link = [];
                for (let i = 1; i < module_length; i++) {
                    module_series_link.push({
                        source: i-1,
                        target: i
                    });
                }
                return module_series_link
            },
        },
        computed: {
            data: function () {
                return {
                    title: {
                        text: '',
                        subtext: '---',
                        left: 'center',
                        align: 'right'
                    },
                    tooltip: {},
                    animationDurationUpdate: 500,
                    animationEasingUpdate: 'quinticInOut',
                    series: [
                        {
                            type: 'graph',
                            layout: 'none',
                            symbolSize: 100,
                            // roam: true,
                            label: {
                                show: true
                            },
                            edgeSymbol: ['', 'arrow'],
                            data: this.getTaskModules(),
                            links: this.getModuleLinks(),
                            lineStyle: {
                                width: 4,
                                curveness: 0
                            }
                        }
                    ]
                }
            }
        }
    }
</script>

<style scoped>
    .chart {
        height: 550px;
    }
</style>
